import React from 'react';
import { useAuth0 } from '@auth0/auth0-react';

function AuthWrapper({ children }) {
  const { isLoading, error } = useAuth0();
  if (isLoading) {
    return <>{children}</>;
  }
  if (error) {
    return <>{children}</>;
  }
  return <>{children}</>;
}


export default AuthWrapper;
